:root {
    --fg: #17181c;
    --shade1: #727274;
    --shade2: #cccdd1;
    --shade3: #f3f4f8;
    --shade4: #ffffff;
    --dur1: 1s;
    --dur2: 6s;
    /*font-size: calc(16px + (32 - 16)*(100vw - 320px)/(2560 - 320));*/
}

#main {
    margin: auto;
    position: fixed;
    z-index: 10;

    background-image: linear-gradient(145deg,#f3f4f8,#cccdd1);

    background-image: linear-gradient(145deg,var(--shade3),var(--shade2));
    color: #17181c;
    color: var(--fg);
    display: flex;
    font: 1em/1.5 Hind, sans-serif;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
}
.preloader {
    -webkit-animation: largePopOut 1s linear;
            animation: largePopOut 1s linear;
    -webkit-animation: largePopOut var(--dur1) linear;
            animation: largePopOut var(--dur1) linear;
    border-radius: 50%;
    box-shadow:
            0.15em 0.15em 0.15em #ffffff inset,
            -0.15em -0.15em 0.15em #727274 inset,
            1em 1em 2em #727274,
            -1em -1em 2em #ffffff;
    box-shadow:
            0.15em 0.15em 0.15em var(--shade4) inset,
            -0.15em -0.15em 0.15em var(--shade1) inset,
            1em 1em 2em var(--shade1),
            -1em -1em 2em var(--shade4);
    margin-bottom: 3em;
    position: relative;
    width: 12em;
    height: 12em;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.preloader__square {
    -webkit-animation: smallPopOut1 1s linear, popInOut 6s 1s linear infinite;
            animation: smallPopOut1 1s linear, popInOut 6s 1s linear infinite;
    -webkit-animation: smallPopOut1 var(--dur1) linear, popInOut var(--dur2) var(--dur1) linear infinite;
            animation: smallPopOut1 var(--dur1) linear, popInOut var(--dur2) var(--dur1) linear infinite;
    border-radius: 0.5em;
    box-shadow:
            0.15em 0.15em 0.15em #ffffff inset,
            -0.15em -0.15em 0.15em #727274 inset,
            0.25em 0.25em 0.5em #727274,
            -0.25em -0.25em 0.5em #ffffff;
    box-shadow:
            0.15em 0.15em 0.15em var(--shade4) inset,
            -0.15em -0.15em 0.15em var(--shade1) inset,
            0.25em 0.25em 0.5em var(--shade1),
            -0.25em -0.25em 0.5em var(--shade4);
    position: absolute;
    top: 2.5em;
    left: 2.5em;
    width: 3em;
    height: 3em;
}
.preloader__square:nth-child(n + 2):nth-child(-n + 3) {
    left: 6.5em;
}
.preloader__square:nth-child(n + 3) {
    top: 6.5em;
}
.preloader__square:nth-child(2) {
    -webkit-animation: smallPopOut2 1s linear, move2 6s 1s linear infinite;
            animation: smallPopOut2 1s linear, move2 6s 1s linear infinite;
    -webkit-animation: smallPopOut2 var(--dur1) linear, move2 var(--dur2) var(--dur1) linear infinite;
            animation: smallPopOut2 var(--dur1) linear, move2 var(--dur2) var(--dur1) linear infinite;
}
.preloader__square:nth-child(3) {
    -webkit-animation: smallPopOut3 1s linear, move3 6s 1s linear infinite;
            animation: smallPopOut3 1s linear, move3 6s 1s linear infinite;
    -webkit-animation: smallPopOut3 var(--dur1) linear, move3 var(--dur2) var(--dur1) linear infinite;
            animation: smallPopOut3 var(--dur1) linear, move3 var(--dur2) var(--dur1) linear infinite;
}
.preloader__square:nth-child(4) {
    -webkit-animation: smallPopOut4 1s linear, move4 6s 1s linear infinite;
            animation: smallPopOut4 1s linear, move4 6s 1s linear infinite;
    -webkit-animation: smallPopOut4 var(--dur1) linear, move4 var(--dur2) var(--dur1) linear infinite;
            animation: smallPopOut4 var(--dur1) linear, move4 var(--dur2) var(--dur1) linear infinite;
}
.status {
    -webkit-animation: fadeIn 1s linear forwards;
            animation: fadeIn 1s linear forwards;
    -webkit-animation: fadeIn var(--dur1) linear forwards;
            animation: fadeIn var(--dur1) linear forwards;
    text-align: center;
}
.status__dot {
    -webkit-animation: appear1 1s 1s steps(1,start) infinite;
            animation: appear1 1s 1s steps(1,start) infinite;
    -webkit-animation: appear1 var(--dur1) var(--dur1) steps(1,start) infinite;
            animation: appear1 var(--dur1) var(--dur1) steps(1,start) infinite;
    display: inline-block;
}
.status__dot:nth-child(2) {
    -webkit-animation: appear2 1s 1s steps(1,start) infinite;
            animation: appear2 1s 1s steps(1,start) infinite;
    -webkit-animation: appear2 var(--dur1) var(--dur1) steps(1,start) infinite;
            animation: appear2 var(--dur1) var(--dur1) steps(1,start) infinite;
}
.status__dot:nth-child(3) {
    -webkit-animation: appear3 1s 1s steps(1,start) infinite;
            animation: appear3 1s 1s steps(1,start) infinite;
    -webkit-animation: appear3 var(--dur1) var(--dur1) steps(1,start) infinite;
            animation: appear3 var(--dur1) var(--dur1) steps(1,start) infinite;
}


