@-webkit-keyframes largePopOut {
    from, 20% {
        box-shadow:
                0 0 0 #ffffff inset,
                0 0 0 #727274 inset,
                0 0 0 #727274,
                0 0 0 #ffffff;
        box-shadow:
                0 0 0 var(--shade4) inset,
                0 0 0 var(--shade1) inset,
                0 0 0 var(--shade1),
                0 0 0 var(--shade4);
    }
    40% {
        box-shadow:
                0.15em 0.15em 0.15em #ffffff inset,
                -0.15em -0.15em 0.15em #727274 inset,
                2em 2em 2em #727274,
                -2em -2em 4em #ffffff;
        box-shadow:
